import React, { useState } from 'react'
import "../assets/css/userReg.css"
import Input from './Input'
import Button from './Button'
//Validation
import Grid from '@material-ui/core/Grid';
import {Formik , Form , Field, ErrorMessage} from "formik"
import Cookies from 'js-cookies'
import * as Yup from "yup"
import axios from 'axios'

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
toast.configure()

function UserReg(props) {
 // The first commit of Material-UI
    const notify = () => toast.info("Hesabnz mvffqiyytl yaradld!");
    const notifyW = () => toast.error("Daxil etdiyiniz mlumatlar yanldr!");

    const token = Cookies.getItem('XSRF-TOKEN')
    const headers = {
    "X-CSRF-TOKEN":token
    }
    const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/
    const [Error, setError] = useState(false)
    
    const onSubmit =  (values) => {
        axios.post('https://jobday.az/api/register-jobseeker', {name: values.name , surname: values.surname, email: values.email ,  phone: values.phone , password: values.password,  password_confirmation:values.confirmPassword } , headers)
        .then(res => ( res.status === 200 &&   localStorage.setItem("LoginUserData" , JSON.stringify(res.data)) ,notify(), window.location.href = "/" )) 
        .catch(err => {setError(err)} )
    }

    const initialValues = {
        name:'',
        surname:'',
        email:'',
        password:'',
        confirmPassword:'',
    }
    const passRegex = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$/

    const validationSchema = Yup.object({
        name: Yup.string().required(sessionStorage.getItem('lang') === "AZ" && `Adnz daxil edin` || sessionStorage.getItem('lang') === "EN" && `Enter name` || sessionStorage.getItem('lang') === "RU" && ` `),
        surname: Yup.string().required(sessionStorage.getItem('lang') === "AZ" && `Soyadnz daxil edin` || sessionStorage.getItem('lang') === "EN" && `Enter surname` || sessionStorage.getItem('lang') === "RU" && ` `),
        email: Yup.string().email(sessionStorage.getItem('lang') === "AZ" && `Elektron potunuzu dzgn daxil edin` || sessionStorage.getItem('lang') === "EN" && `Enter your email correctly` || sessionStorage.getItem('lang') === "RU" && `     `).required(sessionStorage.getItem('lang') === "AZ" && `Elektron potunuzu daxil edin` || sessionStorage.getItem('lang') === "EN" && `Enter your email` || sessionStorage.getItem('lang') === "RU" && `   `),
        password: Yup.string().matches(passRegex , (sessionStorage.getItem('lang') === "AZ" && `ifrniz n az 8 simvol 1 byk hrf 1 kiik hrf v 1 rqm tkil etmlidir` || sessionStorage.getItem('lang') === "EN" && `Your password must be at least 8 characters, 1 uppercase letter, 1 lowercase letter, and 1 digit` || sessionStorage.getItem('lang') === "RU" && `        8 , 1  , 1    1 .`) ).required(sessionStorage.getItem('lang') === "AZ" && `ifrnizi daxil edin` || sessionStorage.getItem('lang') === "EN" && `Enter password` || sessionStorage.getItem('lang') === "RU" && ` `),
        confirmPassword: Yup.string()
        .oneOf([Yup.ref('password'), null], (sessionStorage.getItem('lang') === "AZ" && `ifrlr uyun deyil` || sessionStorage.getItem('lang') === "EN" && `Passwords do not match` || sessionStorage.getItem('lang') === "RU" && `  `)).required(sessionStorage.getItem('lang') === "AZ" && `ifrnizi daxil edin` || sessionStorage.getItem('lang') === "EN" && `Enter password` || sessionStorage.getItem('lang') === "RU" && ` `)
    })



    return (
        <Formik initialValues={initialValues} validationSchema={validationSchema} onSubmit={onSubmit} validateOnChange={true} validateOnBlur={false}>
            <Form  className="formUser" method="POST">
                
                <div className="errors">
                    <Field placeholder={sessionStorage.getItem('lang') === "AZ" && `Adnz ` || sessionStorage.getItem('lang') === "EN" && `Name ` || sessionStorage.getItem('lang') === "RU" && ` `}  name="name" className="input"/>
                    <ErrorMessage name="name"/>
                </div>
                
                <div className="errors">
                    <Field placeholder={sessionStorage.getItem('lang') === "AZ" && ` Soyadnz` || sessionStorage.getItem('lang') === "EN" && ` Surname` || sessionStorage.getItem('lang') === "RU" && ` `}  name="surname" className="input"/>
                    <ErrorMessage name="surname"/>
                </div>
                
                <div className="errors">
                    <Field placeholder={sessionStorage.getItem('lang') === "AZ" && `Elektron pot nvan` || sessionStorage.getItem('lang') === "EN" && `Email address` || sessionStorage.getItem('lang') === "RU" && `  `} name="email" className="input"/>
                    <ErrorMessage name="email"/>
                </div>
                
                <div className="errors">
                    <Field placeholder={sessionStorage.getItem('lang') === "AZ" && `ifr` || sessionStorage.getItem('lang') === "EN" && `Password` || sessionStorage.getItem('lang') === "RU" && ``}  type="password" name="password" className="input"/>
                    <ErrorMessage name="password"/>
                </div>
                
                <div className="errors">
                    <Field placeholder={sessionStorage.getItem('lang') === "AZ" && `ifrni Tsdiql` || sessionStorage.getItem('lang') === "EN" && `Confirm Password` || sessionStorage.getItem('lang') === "RU" && ` `} type="password" name="confirmPassword" className="input"/>
                    <ErrorMessage name="confirmPassword"/>
                </div>
                
                <div className="errors  submitBtn">
                    <Button type="submit" text={sessionStorage.getItem('lang') === "AZ" && `Daxil edin` || sessionStorage.getItem('lang') === "EN" && `Submit` || sessionStorage.getItem('lang') === "RU" && `B`}  backgroundColor="#3D92A7"  color="white"/>
                    {Error &&  (sessionStorage.getItem('lang') === "AZ" && `Daxil etdiyiniz mlumatlar yanldr. ` || sessionStorage.getItem('lang') === "EN" && `The information you entered is incorrect.` || sessionStorage.getItem('lang') === "RU" && `   .`)}
                </div>
            </Form>
        </Formik>
    )
}

export default UserReg
