import React, { useEffect, useState } from 'react'
import x from '../assets/image/x.png'
import "../assets/css/loginModal.css"
import Input from './Input'
import Button from './Button'
import { Link } from 'react-router-dom'
import {Formik , Form , Field, ErrorMessage} from "formik"
import Cookies from 'js-cookie';
import Cryptr from 'cryptr'
import * as Yup from "yup"
import axios from 'axios'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
toast.configure()

function LoginModal(props) {
    const notify = () => toast.info(sessionStorage.getItem('lang') === "AZ" && `Hesabnza daxil oldunuz!` || sessionStorage.getItem('lang') === "EN" && `You have logged in to your account!` || sessionStorage.getItem('lang') === "RU" && `     !`);
    const {setLoginUserData} = props

    const clickHandler = () => {
        props.modalCloser()
        props.modalOpener()
    }
    
    const clickHandlerForClose = () => {
        props.modalCloser()
    }

    const validationSchema = Yup.object({
        email: Yup.string().email(sessionStorage.getItem('lang') === "AZ" && `Elektron potunuzu dzgn daxil edin` || sessionStorage.getItem('lang') === "EN" && `Enter your email correctly` || sessionStorage.getItem('lang') === "RU" && `     `).required(sessionStorage.getItem('lang') === "AZ" && `Elektron potunuzu daxil edin` || sessionStorage.getItem('lang') === "EN" && `Enter your email` || sessionStorage.getItem('lang') === "RU" && `   `),
        password: Yup.string().required(sessionStorage.getItem('lang') === "AZ" && `ifrnizi daxil edin` || sessionStorage.getItem('lang') === "EN" && `Enter password` || sessionStorage.getItem('lang') === "RU" && ` `),
    })
    
    const token = Cookies.get('XSRF-TOKEN') // => 'value'
    const headers = {
        "X-CSRF-TOKEN": token
    }
    
    const [Error, setError] = useState(false)
    const onSubmit =  (values) => {
        axios.post('https://jobday.az/api/login', {email:values.email , password:values.password} , headers )
        .then(res => ( res.status === 200 && setLoginUserData(res.data.user) ,  localStorage.setItem("LoginUserData" , JSON.stringify(res.data.user)) ,notify(), window.location.href = "/" ) )
        .catch(err => setError(true))
    }
    
    const initialValues = {
        email:'',
        password:'',
    }

    return (
        <div className="loginModal" >
               <Link to="/" ><button onClick={() => clickHandlerForClose()} className="closeImg" ><img  src={x} alt="" width="19" height="auto" /></button></Link>
               <p className="title">{sessionStorage.getItem('lang') === "AZ" && `Giri` || sessionStorage.getItem('lang') === "EN" && `Login` || sessionStorage.getItem('lang') === "RU" && `A`}</p> 
               
               <Formik initialValues={initialValues} validationSchema={validationSchema} onSubmit={onSubmit} validateOnChange={true} validateOnBlur={true}>
                    <Form action="" method="post" >
                        <div className="errors">
                            <Field type="email" placeholder={sessionStorage.getItem('lang') === "AZ" && `Elektron pot` || sessionStorage.getItem('lang') === "EN" && `Email` || sessionStorage.getItem('lang') === "RU" && ` `} name="email"  className="input"/>    
                            <ErrorMessage name="email"/>
                        </div>
                        <div className="errors">
                            <Field type="password" placeholder={sessionStorage.getItem('lang') === "AZ" && `ifrniz` || sessionStorage.getItem('lang') === "EN" && `Password` || sessionStorage.getItem('lang') === "RU" && ``} name="password" className="input"/>    
                            <ErrorMessage name="password" />
                        </div>
                        <div className="errors">
                            <Button text={sessionStorage.getItem('lang') === "AZ" && `Daxil olun` || sessionStorage.getItem('lang') === "EN" && `Submit` || sessionStorage.getItem('lang') === "RU" && ``} type="submit" backgroundColor="#3D92A7"/>
                            {Error &&  (sessionStorage.getItem('lang') === "AZ" && `Daxil etdiyiniz mlumatlar yanldr. ` || sessionStorage.getItem('lang') === "EN" && `The information you entered is incorrect.` || sessionStorage.getItem('lang') === "RU" && `   .`)}
                        </div>
                    </Form>
               </Formik>
               <p className="link">{sessionStorage.getItem('lang') === "AZ" && `Hesabnz yoxdur? ` || sessionStorage.getItem('lang') === "EN" && `Don't have an account?` || sessionStorage.getItem('lang') === "RU" && ` ?`} <button onClick={() => clickHandler()}>{sessionStorage.getItem('lang') === "AZ" && `Qeydiyyatdan kein ` || sessionStorage.getItem('lang') === "EN" && `Create Account` || sessionStorage.getItem('lang') === "RU" && ``}</button></p>
        </div>
    )
}

export default LoginModal
