



export const worktime_translator = (text , lang) => {
    if(lang === 'AZ')
    {
        return text
    }
    else if (lang === 'EN')
    {
        if(text==='Tam i gn')
        {
            return "Full time"
        }
        else if(text==='Nvbli il' || text==='Nvbli i gn') 
        {
            return "Shift work"
        }
        else if(text==='Yar i gn') 
        {
            return "Part time"
        }
        else if(text==='Srbst i qrafiki') 
        {
            return "Freelance"
        }
        else if(text==='Uzaqdan i') 
        {
            return "Remote"
        }
        else
        {
            return "" 
        }
        
    }
    else if (lang === 'RU')
    {
        if(text==='Tam i gn')
        {
            return "  "
        }
        else if(text==='Nvbli il' || text==='Nvbli i gn') 
        {
            return " "
        }
        else if(text===' ') 
        {
            return "Part time"
        }
        else if(text==='Srbst i qrafiki') 
        {
            return ""
        }
        else if(text==='Uzaqdan i') 
        {
            return ""
        }
        else
        { 
            return "" 
        }
    }
    else 
    {}
}


